@charset "UTF-8";
body {
  font-family: "ヒラギノ明朝 ProN";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__inner {
  padding-top: 1.25rem;
}

.header__logo {
  font-family: "PT Serif", serif;
  font-size: 1.5625rem;
  color: #fff;
  letter-spacing: 0.1rem;
}

.main-visual__img {
  position: relative;
}
.main-visual__img:before {
  content: "";
  background: url(../../images/Sa.svg);
  width: 16.375rem;
  height: 15rem;
  position: absolute;
  right: 0;
  bottom: -1.5625rem;
}